._3qgzY {
  position: relative;
  padding-left: calc(var(--width) + 8px);
}

._3qgzY::before {
  position: absolute;
  content: '';
  border-radius: 50px;
  background-color: var(--color);
  width: var(--width);
  height: var(--height);
  top: var(--top);
  left: var(--left);
  border: var(--border);
}

._2IYfc {
  white-space: pre;
  overflow: hidden;
}
